<style>

.topBtnWrap {
    display: flex;
    justify-content: space-between;
}

* {
    list-style: none;
    padding: 0;
    margin: 0;
}

.fu {
    padding: 10px;
}

.master-content-middle {
    display: -webkit-flex;
    /* Safari */
    display: flex;
    padding: 30px 10px;
    border: 1px solid #d7f0f2;
    align-items: center;
    /*margin-top: 10px;*/
}

.master-content-middle ul .master-content-middle-child {
    display: inline-block;
    margin-left: 20px;
    overflow: hidden;
}

.seacrh-wrap-li {
    overflow: hidden;
}

.label-left {
    float: left;
    line-height: 32px;
    display: inline-block;
    width: 60px;
}

.ivu-page {
    text-align: right;
    margin-right: 30px;
    padding: 10px 10px;
}

.vertical-center-modal {
    display: flex;
    align-items: center;
    justify-content: center;
}

.native-wrap {
    display: -webkit-flex;
    /* Safari */
    display: flex;
    justify-content: space-around;
}

.op-fxAdDeliverId {
    margin-right: 50px;
}

</style>

<template>

<div>

    <top-title title="物料管理"></top-title>


    <div class="fu">
        <div class="topBtnWrap">
            <div>
                <Button class="btnColor" icon="plus" @click="nativeBol">新建广告物料</Button>
                <Button class="btnCancel" type="error" :disabled="isDeletes" @click="deleteAll">删除</Button>
                <Button type="ghost" :icon="sxIcon" @click="sx">筛选</Button>

            </div>
            <div>
                <Input v-model="nameLike" placeholder="请输入广告物料名称" style="width: 300px"></Input>
                <Button class="btnColor" icon="search" @click="search">搜索</Button>
            </div>
        </div>
        <div class="master-content-middle" v-show="isShow" style="margin-top: 10px;">
            <ul>
                <li class="master-content-middle-child">
                    <span class="label-left">载体类型：</span>
                    <div class="main-element">
                        <el-select style="width: 200px;" v-model="isNative" filterable placeholder="请选择" @change="getFilterData">
                            <el-option :value="-99" :label="`全部`"></el-option>
                            <el-option :value="1" :label="`原生`"></el-option>
                            <el-option :value="0" :label="`非原生`"></el-option>
                        </el-select>
                    </div>
                    <!-- <Select v-model="isNative" style="width:200px" filterable @on-change="getFilterData">
                        <Option value='-99'>全部</Option>
                        <Option value='1'>原生</Option>
                        <Option value='0'>非原生</Option>
                    </Select> -->
                </li>

                <li class="master-content-middle-child">
                    <span class="label-left">广告：</span>
                    <!-- <Select style="width:200px" filterable clearable @on-change='selectAdId' placeholder="请选择(可搜索)">
                        <Option value='-99'>
                            <span class="orderName fu">全部</span>
                        </Option>
                        <Option v-for="(item,index) in deliverList" :key="index" :value='item.fxAdDeliverId'>
                            <span class="op-deliverName">{{item.deliverName}}</span>
                        </Option>
                        <Button @click="loadMore" type="text" long>{{textLoadMore}}</Button>
                    </Select> -->
                    <div class="main-element">
                        <el-select style="width: 200px;" v-model="adModel" filterable placeholder="请选择" @change="selectAdId">
                            <el-option :value="-99" :label="`全部`"></el-option>
                            <el-option v-for="(item,index) in deliverList" :key="index" :label="item.deliverName" :value="item.fxAdDeliverId">
                            </el-option>
                            <Button @click="loadMore" type="text" long>{{textLoadMore}}</Button>
                        </el-select>
                    </div>
                </li>


                <li class="master-content-middle-child">
                    <span class="label-left">物料状态：</span>
                    <!-- <Select v-model="status" style="width:200px" filterable @on-change="getFilterData2">
                        <Option value='-99'>全部</Option>
                        <Option value='1'>启用</Option>
                        <Option value='0'>删除</Option>
                    </Select> -->
                    <div class="main-element">
                        <el-select style="width: 200px;" v-model="status" placeholder="请选择" @change="getFilterData2">
                            <el-option :value="-99" :label="`全部`"></el-option>
                            <el-option :value="1" :label="`启用`"></el-option>
                            <el-option :value="0" :label="`删除`"></el-option>
                        </el-select>
                    </div>
                </li>

            </ul>

        </div>
        <div class="table-wrap">
            <Table :loading="loading" :columns="columns" :data="data" border @on-selection-change="selectData" style="margin-top:10px;"></Table>
            <Page :current="pageNo" @on-change='clickPage' :total="totalCount" show-total></Page>
        </div>

        <Modal v-model="modal2" width="500" class-name="vertical-center-modal">
            <p slot="header" style="text-align:center">
                <span>选择新建广告载体</span>
            </p>
            <div class="native-wrap" style="text-align:center">
                <Button class="btnColor" @click="newNativeMaterial" type="primary">原生</Button>
                <Button class="btnColor" @click="newNoNativeMaterial" type="primary">非原生</Button>
            </div>
            <div slot="footer">
            </div>
        </Modal>

        <Modal v-model="modal3" width="500" class-name="vertical-center-modal">
            <p slot="header" style="text-align:center">
                <span>选择新建广告物料来源</span>
            </p>
            <div class="native-wrap" style="text-align:center">
                <Button class="btnColor" @click="newFx168Material" type="primary">FX168广告</Button>
                <Button class="btnColor" @click="newBeiyeAdMaterial" type="primary">倍页广告</Button>
            </div>
            <div slot="footer">
            </div>
        </Modal>
        <new-fx168-material ref="newFx168Material" v-on:filterParent="initTableList"></new-fx168-material>
        <update-fx168-material ref="updateFx168Material" v-on:filterParent="initTableList"></update-fx168-material>
        <update-beiye-material ref="updateBeiyeMaterial" v-on:filterParent="initTableList"></update-beiye-material>
        <new-NoNative ref="newNoNative" v-on:filterParent="initTableList"></new-NoNative>
        <show-fx168-material ref="showFx168Material"></show-fx168-material>
        <show-beiye-material ref="showBeiyeMaterial"></show-beiye-material>
        <updata-Nonative ref="updataNonative" v-on:filterParent="initTableList"></updata-Nonative>
        <show-Nonative ref="showNonative"></show-Nonative>
        <new-beiye-material ref="newBeiyeMaterial" v-on:filterParent="initTableList"></new-beiye-material>
    </div>
</div>

</template>

<script>

import topTitle from '@/components/common/topTitle'
import newFx168Material from './child/newFx168Material.vue'
import newBeiyeMaterial from './child/newBeiyeMaterial.vue'
import updateFx168Material from './child/updateFx168Material.vue'
import updateBeiyeMaterial from './child/updateBeiyeMaterial.vue'
import newNoNative from './child/newNoNative.vue'
import showFx168Material from './child/showFx168Material.vue'
import showBeiyeMaterial from './child/showBeiyeMaterial.vue'
import updataNonative from './child/updataNoNative.vue'
import showNonative from './child/showNoNative.vue'

export default {
    components: {
        topTitle,
        newFx168Material,
        updateFx168Material,
        updateBeiyeMaterial,
        newNoNative,
        showFx168Material,
        showBeiyeMaterial,
        updataNonative,
        showNonative,
        newBeiyeMaterial
    },
    data() {
        return {
            adModel: "",
            sxIcon: 'chevron-down',
            nameLike: '',
            status: 1,
            isNative: '',
            fxAdDeliverId: '',
            pageSize: 10,
            pageNo: 1,
            deliverListPageNo: 1,
            deliverListTotalPages: 1,
            textLoadMore: '加载更多',
            isShow: false,
            disabled: true,
            loading: true,
            modal2: false,
            modal3: false,
            isAdds: true,
            isDeletes: true,
            pageNo: 1,
            totalCount: 0,
            materialId: '',
            addMateriaTempParams: [],
            deleteMateriaTempParams: [],
            data: [],
            deliverList: [],
            columns: [{
                type: 'selection',
                width: 60,
                align: 'center'

            }, {
                title: '广告物料名称',
                key: 'materialTemplateName',
            }, {
                title: '物料ID',
                key: 'materialId',

            }, {
                title: '尺寸',
                key: 'materialSize',
            }, {
                title: '载体类型',
                key: 'type'
            }, {
                title: '操作',
                key: 'operation',
                width: 220,
                align: 'center',
                render: (h, params) => {

                    if (params.row.materialStatus == 0) {
                        return h('div'), [

                            h('Button', {
                                props: {
                                    type: 'info',
                                    size: 'small'
                                },
                                style: {
                                    marginRight: '5px',
                                    backgroundColor: '#19be6b',
                                    borderColor: '#19be6b'
                                },
                                on: {
                                    click: () => {
                                        if (params.row.isNative === 0) {
                                            this.$refs.updataNonative.beforeLoad(params.row.materialStatus, 1);
                                            this.$refs.updataNonative.getMaterialById(params.row.materialId)
                                        } else {
                                            if (params.row.sourceFrom === 0) {
                                                this.$refs.updateFx168Material.beforeLoad(params.row.materialStatus, 1);
                                                this.$refs.updateFx168Material.getMaterialById(params.row.materialId)
                                            } else if (params.row.sourceFrom === 1) {
                                                this.$refs.updateBeiyeMaterial.beforeLoad(params.row.materialStatus, 1);
                                                this.$refs.updateBeiyeMaterial.getMaterialById(params.row.materialId)
                                            }
                                        }
                                    }
                                }
                            }, '复制'),

                            h('Button', {
                                props: {
                                    type: 'primary',

                                    size: 'small'
                                },
                                style: {
                                    marginRight: '5px',
                                    backgroundColor: '#0676c4',
                                    borderColor: '#0676c4'
                                },
                                on: {
                                    click: () => {
                                        this.modal2 = false;
                                        if (params.row.isNative === 0) {
                                            this.$refs.showNonative.beforeLoad();
                                            this.$refs.showNonative.getMaterialById(params.row.materialId)
                                        } else {
                                            if (params.row.sourceFrom === 0) {
                                                this.$refs.showFx168Material.beforeLoad();
                                                this.$refs.showFx168Material.getMaterialById(params.row.materialId)
                                            } else if (params.row.sourceFrom === 1) {
                                                this.$refs.showBeiyeMaterial.beforeLoad();
                                                this.$refs.showBeiyeMaterial.getMaterialById(params.row.materialId)
                                            }
                                        }

                                    }
                                }
                            }, '查看'),
                        ]
                    }
                    return h('div'), [

                        h('Button', {
                            props: {
                                type: 'info',
                                size: 'small'
                            },
                            style: {
                                marginRight: '5px',
                                backgroundColor: '#19be6b',
                                borderColor: '#19be6b'
                            },
                            on: {
                                click: () => {
                                    if (params.row.isNative === 0) {
                                        this.$refs.updataNonative.beforeLoad(params.row.materialStatus, 1);
                                        this.$refs.updataNonative.getMaterialById(params.row.materialId)
                                    } else {
                                        if (params.row.sourceFrom === 0) {
                                            this.$refs.updateFx168Material.beforeLoad(params.row.materialStatus, 1);
                                            this.$refs.updateFx168Material.getMaterialById(params.row.materialId)
                                        } else if (params.row.sourceFrom === 1) {
                                            this.$refs.updateBeiyeMaterial.beforeLoad(params.row.materialStatus, 1);
                                            this.$refs.updateBeiyeMaterial.getMaterialById(params.row.materialId)
                                        }
                                    }
                                }
                            }
                        }, '复制'),


                        h('Button', {
                            props: {
                                type: 'primary',

                                size: 'small'
                            },
                            style: {
                                marginRight: '5px',
                                backgroundColor: '#0676c4',
                                borderColor: '#0676c4'
                            },
                            on: {
                                click: () => {
                                    this.modal2 = false;
                                    if (params.row.isNative === 0) {
                                        this.$refs.showNonative.beforeLoad();
                                        this.$refs.showNonative.getMaterialById(params.row.materialId)
                                    } else {
                                        if (params.row.sourceFrom === 0) {
                                            this.$refs.showFx168Material.beforeLoad();
                                            this.$refs.showFx168Material.getMaterialById(params.row.materialId)
                                        } else if (params.row.sourceFrom === 1) {
                                            this.$refs.showBeiyeMaterial.beforeLoad();
                                            this.$refs.showBeiyeMaterial.getMaterialById(params.row.materialId)
                                        }
                                    }

                                }
                            }
                        }, '查看'),
                        h('Button', {
                            props: {
                                type: 'info',

                                size: 'small'
                            },
                            style: {
                                marginRight: '5px',
                                backgroundColor: '#1fa2fd',
                                borderColor: '#1fa2fd'
                            },
                            on: {
                                click: () => {
                                    if (params.row.isNative === 0) {
                                        this.$refs.updataNonative.beforeLoad();
                                        this.$refs.updataNonative.getMaterialById(params.row.materialId)
                                    } else {
                                        if (params.row.sourceFrom === 0) {
                                            this.$refs.updateFx168Material.beforeLoad();
                                            this.$refs.updateFx168Material.getMaterialById(params.row.materialId)
                                        } else if (params.row.sourceFrom === 1) {
                                            this.$refs.updateBeiyeMaterial.beforeLoad();
                                            this.$refs.updateBeiyeMaterial.getMaterialById(params.row.materialId)
                                        }
                                    }

                                }
                            }
                        }, '修改'),
                        h('Button', {
                            props: {
                                type: 'error',
                                size: 'small'
                            },
                            style: {
                                marginRight: '5px',
                                backgroundColor: '#ea0b00',
                                borderColor: '#ea0b00'
                            },
                            on: {
                                click: () => {
                                    // this.materialId = params.row.materialId
                                    //     this.deleteModal(this.materialId,params.index);
                                    let v = [];
                                    v.push(params.row);

                                    this.deleteBox(v);
                                }
                            }
                        }, '删除'),

                    ]
                }
            }],
        }
    },
    created() {
        this.initTableList();
        this.getDeliverList();
    },
    methods: {
        selectData: function(selection) {
            console.log(selection)
            let canDeletes = selection.filter((item) => {
                return item.materialStatus == 1;
            });

            let canAdds = selection.filter((item) => {
                return item.materialStatus == 0;
            });
            console.log(canAdds)
            this.isAdds = !canAdds.length > 0;
            this.addMateriaTempParams = canAdds;
            this.isDeletes = !canDeletes.length > 0;
            this.deleteMateriaTempParams = canDeletes;
        },

        deleteBox(params) {
            console.log(params);
            let materialNames = params.map((item, index) => {
                return item.materialTemplateName;
            });
            let materialIds = params.map((item, index) => {
                return item.materialId;
            });
            this.$Modal.confirm({
                title: '删除广告物料',
                content: `你确定要删除` + materialNames.join(","),
                onOk: () => {
                    this.deleteMaterialData(materialIds.join(","));
                    console.log(materialIds.join(","))
                }
            })
        },
        getFilterData: function(data) {
            this.isNative = data
            this.pageSize = 10;
            this.pageNo = 1;
            this.initTableList();
        },
        getFilterData2: function(data) {
            this.status = data
            this.pageSize = 10;
            this.pageNo = 1;
            this.initTableList();
        },

        deleteMaterialData(materialId) {
            const url = `/material/deleteMaterial.json?materialId=${materialId}`;
            console.log(materialId)
            this.$Http.get(url, {}).then(res => {
                console.log(res)
                this.$Message.info(res.body.data);
                this.initTableList();
            })
        },

        deleteAll() {
            this.deleteBox(this.deleteMateriaTempParams);

        },

        getDeliverList() {
            this.deliverList = [];
            const url = "/deliver/getDeliverList.json?nameLike=&isNative=&orderId=&codeGroupId=&codeId=&materialId=&startDate=&endDate=&status=&pageSize=10&pageNo=" + this.deliverListPageNo;
            this.$Http.get(url, {}).then(res => {
                console.log(res)
                let arr = res.body.data.result;
                if (this.deliverListPageNo == 1) {
                    this.deliverList = arr;
                } else {
                    for (var j = 0; j < arr.length; j++) {
                        this.deliverList.push(arr[j]);
                    }
                }
                this.deliverListTotalPages = res.body.data.totalPages;
            })
        },


        loadMore() {
            if (this.deliverListPageNo == this.deliverListTotalPages) {
                this.textLoadMore = "已到最底部"
                return false;
            } else {
                this.deliverListPageNo++;
            }
            this.getDeliverList();
        },

        newFx168Material() {
            this.modal3 = false;
            this.$refs.newFx168Material.beforeLoad();
        },
        newBeiyeAdMaterial() {
            this.modal3 = false;
            this.$refs.newBeiyeMaterial.beforeLoad();

        },
        newNativeMaterial() {
            this.modal2 = false;
            this.modal3 = true;
        },
        newNoNativeMaterial() {
            this.modal2 = false;
            this.$refs.newNoNative.beforeLoad();
        },

        sx() {
            this.isShow = !this.isShow;
            if (this.isShow) {
                this.sxIcon = "chevron-up"
            } else {
                this.sxIcon = "chevron-down"
            }
        },
        selectAdId(data) {
            this.pageSize = 10;
            this.pageNo = 1;
            this.fxAdDeliverId = data == undefined ? "" : data;
            this.initTableList();

        },
        onSelect(data) {
            this.pdLength(data.length)
            console.log(data);
            this.paramsObj = data;
            this.strSort(this.paramsObj)
            console.log(this.paramsObj)
        },
        cancelSelect(data) {
            this.pdLength(data.length)
            this.paramsObj = data;
            this.strSort(this.paramsObj)
            console.log(this.paramsObj)
        },
        selectAll(data) {
            // this.paramsObj = data;
            // console.log(this.paramsObj)
        },


        // **************************************
        selectAllChange(data) {
            this.pdLength(data.length)
            this.paramsObj = data;
            this.strSort(this.paramsObj)
            console.log(this.paramsObj)
        },
        // **************************************
        pdLength(length) {
            if (length > 0) {
                this.disabled = false;
            } else {
                this.disabled = true;
            }
        },
        strSort(data) {
            let str = '';
            for (var i = 0; i < data.length; i++) {
                if (i == data.length - 1) {
                    str += data[i].materialId
                } else {

                    str += data[i].materialId + ","
                }
                this.materialId = str;
                console.log(this.materialId);
            }

        },
        search() {
            // alert(this.value)
            this.pageSize = 10;
            this.pageNo = 1;
            this.data = [];
            this.initTableList();
        },
        clickPage(data) {
            console.log(data);
            this.loading = true;
            this.pageNo = data;
            this.data = [];
            this.initTableList();
        },
        nativeBol() {
            this.modal2 = true;
        },
        routeNative() {
            this.$router.push({
                name: 'setNewadMaterial'
            });
        },
        routeNoNative() {
            this.$router.push({
                name: 'setNewadMaterialNoNative'
            });
        },
        initTableList() {
            this.isAdds = true;
            this.isDeletes = true;
            const url = `/material/getMaterialList.json?nameLike=${encodeURIComponent(this.nameLike)}&status=${this.status}&pageSize=${this.pageSize}&pageNo=${this.pageNo}&fxAdDeliverId=${this.fxAdDeliverId}&isNative=${this.isNative}`;
            this.$Http.get(url, {}).then(res => {
                this.loading = false;
                console.log(res)
                let arr = res.body.data.result;
                let tabList = '';
                this.totalCount = res.body.data.totalCount;
                console.log(this.totalCount)
                this.data = [];
                for (let key in arr) {
                    tabList = {
                        'materialTemplateName': arr[key].materialName,
                        'materialId': arr[key].id,
                        'materialIdLook': arr[key].materialIdLook,
                        'materialSize': arr[key].size == "" ? "--" : arr[key].size,
                        'isNative': arr[key].isNative,
                        'type': arr[key].isNative == 0 ? "非原生" : "原生",
                        'materialStatus': arr[key].materialStatus,
                        'adShowUrl': arr[key].adShowUrl,
                        'sourceFrom': arr[key].sourceFrom,
                    }
                    this.data.push(tabList);
                }

            })
        },
        deleteModal(materialId, index) {
            this.$Modal.confirm({
                title: '启用公司',
                content: '你确定要删除：' + materialId,
                onOk: () => {
                    this.deleteM(materialId, index)
                        // this.deleteCustomData(customIds.join(","));
                },
                onCancel() {
                    this.$Message.info("已取消删除：" + materialId);
                }
            })
        },
        deleteM(materialId, index) {
            const url = `/material/deleteMaterial.json?materialId=${materialId}`;
            console.log(materialId)
            this.$Http.get(url, {}).then(res => {
                console.log(res)

            })
        },
        getAdList() {
            const url = `/appConfig/getAppConfigList.json?nameLike=&codeId=&appType=&pageSize=${this.pageSize}&pageNo=${this.pageNo}`;
            this.$Http.get(url, {}).then(res => {
                console.log(res)

            })
        }
    }
}

</script>



